/* =========================================================================
   Base
========================================================================= */
* {
  box-sizing: border-box; }

p {
  line-height: 1.4em;
  max-width: 700px; }

body {
  font-family: Arial, Helvetica, sans-serif;
  color: #4c4c4c; }

img {
  max-width: 100%;
  height: auto; }

h1, h2, h3, h4, h5, h6 {
  font-family: 'Playfair Display', serif; }

.row {
  letter-spacing: -.31em;
  text-rendering: optimizespeed;
  font-family: Helvetica, Arial, sans-serif;
  display: flex;
  flex-flow: row wrap;
  align-content: flex-start;
  margin-left: -10px;
  margin-right: -10px; }
  .row:before, .row:after {
    content: "";
    display: table; }
  .row:after {
    clear: both; }

.blocks .block {
  position: relative;
  display: inline-block;
  zoom: 1;
  letter-spacing: normal;
  word-spacing: normal;
  vertical-align: top;
  text-rendering: auto;
  padding-left: 10px;
  padding-right: 10px;
  box-sizing: border-box;
  width: 100%; }

.blocks .block {
  margin-bottom: 40px; }
  @media (min-width: 576px) {
    .blocks .block {
      width: 50%;
      margin-bottom: 20px; } }
  @media (min-width: 992px) {
    .blocks .block {
      width: 33.33333%; } }
  .blocks .block__title {
    margin: 10px 0 10px 0; }
  .blocks .block__desc {
    line-height: 1.6em; }

/* =========================================================================
   Layout
========================================================================= */
#main-wrap, #main-footer, #main-header {
  padding-left: 20px;
  padding-right: 20px;
  overflow: hidden; }
  #main-wrap:before, #main-wrap:after, #main-footer:before, #main-footer:after, #main-header:before, #main-header:after {
    content: "";
    display: table; }
  #main-wrap:after, #main-footer:after, #main-header:after {
    clear: both; }

#main-header {
  margin-top: 20px; }

#main-content {
  width: 100%;
  transition: all 300ms cubic-bezier(0.45, 0.22, 0.41, 0.92); }
  @media (min-width: 768px) {
    #main-content {
      width: 70%;
      float: left;
      padding-right: 20px; } }

#main-sidebar {
  width: 100%;
  overflow: hidden;
  transition: all 300ms cubic-bezier(0.45, 0.22, 0.41, 0.92); }
  @media (min-width: 768px) {
    #main-sidebar {
      width: 30%;
      float: right;
      padding-left: 20px; } }

@media (min-width: 768px) {
  .hide-sidebar #main-content {
    transition: all 300ms cubic-bezier(0.45, 0.22, 0.41, 0.92);
    width: 100%;
    padding-right: 0; }
  .hide-sidebar #main-sidebar {
    transition: all 300ms cubic-bezier(0.45, 0.22, 0.41, 0.92);
    width: 0;
    opacity: 0; } }

.logo {
  margin: auto;
  height: 100px;
  display: block; }
  .logo-wrap {
    width: 230px;
    margin: auto; }
    @media (min-width: 768px) {
      .logo-wrap {
        float: left; } }
    .logo-wrap:before, .logo-wrap:after {
      content: "";
      display: table; }
    .logo-wrap:after {
      clear: both; }
  .logo a {
    display: block;
    height: 100px; }

/* =========================================================================
   Main nav
========================================================================= */
.main-nav {
  display: block;
  text-align: center;
  margin: 20px 0; }
  @media (max-width: 768px) {
    .main-nav .sidebar-toggle {
      display: none; } }
  @media (min-width: 768px) {
    .main-nav {
      margin-top: 0;
      text-align: left;
      float: left;
      height: 100px;
      line-height: 100px; } }
  .main-nav:before, .main-nav:after {
    content: "";
    display: table; }
  .main-nav:after {
    clear: both; }
  .main-nav ul {
    margin: 0 0 0 20px;
    padding: 0;
    list-style: none; }
    .main-nav ul:before, .main-nav ul:after {
      content: "";
      display: table; }
    .main-nav ul:after {
      clear: both; }
    .main-nav ul li {
      margin: 0 0 10px 0; }
      .main-nav ul li:last-child {
        margin-bottom: 0; }
      @media (min-width: 768px) {
        .main-nav ul li {
          float: left;
          margin: 0 15px 0 0; }
          .main-nav ul li:last-child {
            margin-right: 0; } }
  .main-nav a {
    text-decoration: none;
    color: #6E7A92;
    text-transform: uppercase;
    transition: color 300ms cubic-bezier(0.45, 0.22, 0.41, 0.92); }
    .main-nav a:hover {
      color: #96848D; }
